#wrap {
  position: absolute;
  
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#alien {
  position: absolute;
  width: 100px;
  height: 180px;
  -webkit-animation: walk 1s linear infinite;
          animation: walk 1s linear infinite;
}
#alien:before {
  left: -20px;
  top: -20px;
  width: 120px;
  height: 120px;
  border: 10px solid rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  z-index: 99;
}

.body {
  position: absolute;
  top: 49px;
  width: 100px;
  height: 80px;
  background: #92cbd9;
}
.body:before {
  top: -50px;
  width: 100px;
  height: 100px;
  background: #92cbd9;
  border-radius: 50%;
  clip: rect(0, 100px, 50px, 0);
}
.body:after {
  content: '';
  position: absolute;
  top: 54px;
  width: 100px;
  height: 50px;
  background: #92cbd9;
  border-radius: 50%;
  clip: rect(25px, 100px, 50px, 0);
}

.eyes {
  position: absolute;
  top: 0;
  width: 20px;
  height: 20px;
  background: #6294a0;
  border-radius: 50%;
  -webkit-animation: twinkle 5s infinite;
          animation: twinkle 5s infinite;
}
.eyes.left {
  left: 30px;
}
.eyes.right {
  left: 70px;
}

.mouse {
  position: absolute;
  top: 20px;
  left: 45px;
  width: 30px;
  height: 20px;
  border: 5px solid #6294a0;
  border-radius: 50%;
  box-sizing: border-box;
  clip: rect(10px, 30px, 20px, 0);
}

.arms {
  position: absolute;
  top: 80px;
  width: 80px;
}
.arms:before {
  top: 0;
  width: 80px;
  height: 200px;
  border: 10px solid #92cbd9;
  border-top-width: 20px;
  border-radius: 50%;
  box-sizing: border-box;
}
.arms:after {
  top: 33px;
  width: 12px;
  height: 12px;
  background: #92cbd9;
  border-radius: 50%;
  clip: rect(6px, 12px, 12px, 0);
}
.arms.left {
  left: -30px;
  -webkit-animation: walk-left-hand 1s linear infinite;
          animation: walk-left-hand 1s linear infinite;
}
.arms.left:before {
  clip: rect(0, 35px, 40px, 0);
}
.arms.left:after {
  left: 8px;
}
.arms.right {
  right: -30px;
  -webkit-animation: walk-right-hand 1s linear infinite;
          animation: walk-right-hand 1s linear infinite;
}
.arms.right:before {
  clip: rect(0, 80px, 40px, 45px);
}
.arms.right:after {
  right: 8px;
}

.legs {
  position: absolute;
  top: 140px;
  width: 20px;
}
.legs:before {
  width: 0;
  border: 10px solid transparent;
  border-top: 150px solid #92cbd9;
  clip: rect(0px, 20px, 30px, 0);
}
.legs:after {
  top: 20px;
  left: 2px;
  width: 16px;
  height: 16px;
  background: #92cbd9;
  border-radius: 50%;
  clip: rect(8px, 16px, 16px, 0);
}
.legs.left {
  left: 20%;
  -webkit-animation: walk-left-foot 1s linear infinite;
          animation: walk-left-foot 1s linear infinite;
}
.legs.right {
  right: 20%;
  -webkit-animation: walk-right-foot 1s linear infinite;
          animation: walk-right-foot 1s linear infinite;
}

.halo {
